import React from "react";
import * as restApi from "lib/rest-api";
import { Outer, Logo, ImgContainer } from "./styles";
import { GlobalStyle } from "ui/global";
import { Form, FormGroup, Label, LoginButton } from "ui/form";

export default class FrontPage extends React.Component {
  state = {
    email: "",
    password: ""
  };
  onSubmit = async e => {
    const { email, password } = this.state;
    this.setState({ password: "matolek", user: "lolek" });
    restApi.auth.login({ email, password });
  };
  render() {
    const { email, password } = this.state;

    return (
      <Outer>
        <GlobalStyle />
        <ImgContainer>
          <Logo src="/static/logo-frontpage.svg" />
        </ImgContainer>
        <Form>
          <FormGroup>
            <Label>Email</Label>
            <input
              type="email"
              name="email"
              //   value={username}
              //   onChange={this.onInputChange}
            />
          </FormGroup>
          <FormGroup>
            <Label>Password</Label>
            <input
              type="password"
              name="password"
              //   value={password}
              //   onChange={this.onInputChange}
            />
          </FormGroup>
          <FormGroup button>
            <LoginButton type="button" onClick={this.onSubmit}>
              {/* {loggingIn ? "Processing" : "Login"} */} Login
            </LoginButton>
          </FormGroup>
        </Form>
      </Outer>
    );
  }
}
